import React, { Component } from 'react'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs'
import NApi from '../../Component/NumbersAPI/NApi';
import FAQ from '../../Component/CorporateBond/FAQ';
import SecondAPI from '../../Component/SecondAPI/SecondAPI'
import NCDMain from '../Home/images/NCD/1.png'
import NCDF1 from '../Home/images/NCD/Return.svg'
import NCDF2 from '../Home/images/NCD/Secured.svg'
import NCDF3 from '../Home/images/NCD/Frequency.svg'
import NCDF4 from '../Home/images/NCD/Ratings.svg'
import { withTranslation } from 'react-i18next';

export class NCD extends Component {
    data = {
        "NCD": {
            "Title": "",
            "ProductCategoryId": "9",
            "SebiCategory": "",
            "SebiSubCategory": "",
            "DefaultProductId": "1",
            "faqs": [
                {
                    "heading":"NCD:NCD24",
                    "description": `NCD:NCD25`
                },
                {
                    "heading": "NCD:NCD30",
                    "description": "NCD:NCD31"
                },
                {
                    "heading": "NCD:NCD32",
                    "description": "NCD:NCD33"
                },
                {
                    "heading": "NCD:NCD34",
                    "description": "NCD:NCD35"
                }

            ]
        }

    }
    render() {

        const productinfo = this.data.NCD;
        const{t} = this.props;

        return (
            <>
                <div className='container container-wrapper'>
                    <div className='col-lg-7 col-md-6 mb-4'>
                        <h2>{t("NCD:NCD1")}</h2>
                        <p>{t("NCD:NCD3")}</p>
                        <a href="https://app.nivesh.com/home/index"><button type="button" className="btn btn-primary btn-lg">{t('CTAFooter:InvestNow1')}</button></a>
                    </div>
                    <div className='col-lg-5 col-md-6'>
                        <img src={NCDMain} alt="" className='img-fluid' />
                    </div>
                </div>
                <div className="API-Section">
               <div className="container container-wrapper">
                  <h2 className='text-center'>{t("NCD:NCD3-A")}</h2>
                  <div className='row row justify-content-lg-center'>
                     <div className='col col-lg-10'>
                        <SecondAPI productCategoryId={productinfo.ProductCategoryId} sebiCategory={productinfo.SebiCategory} sebiSubCategory={productinfo.SebiSubCategory} defaultProductId={productinfo.DefaultProductId} />
                     </div>
                  </div>
               </div>
            </div>

                <div className='bg-light'>
                    <div className='container container-wrapper'>
                        <h2 className='text-center'>{t("NCD:NCD4")}</h2>
                        <div className='row'>
                            <div className='col-lg-6 mb-4'>
                                <div className='p-3 bg-white h-100'>
                                    <div className='col-lg-3'>
                                        <img src={NCDF1} alt="" className='img-fluid' />
                                    </div>
                                    <div className='col-lg-9'>
                                        <h3>{t("NCD:NCD5")}</h3>
                                        <p className='small-text'>{t("NCD:NCD6")}</p>
                                    </div>
                                </div>
                            </div>
                            <div className='col-lg-6 mb-4'>
                                <div className='p-3 bg-white h-100'>
                                    <div className='col-lg-3'>
                                        <img src={NCDF2} alt="" className='img-fluid' />
                                    </div>
                                    <div className='col-lg-9'>
                                        <h3>{t("NCD:NCD7")}</h3>
                                        <p className='small-text'>{t("NCD:NCD8")}</p>
                                    </div>
                                </div>
                            </div>
                            <div className='col-lg-6 mb-4'>
                                <div className='p-3 bg-white h-100'>
                                    <div className='col-lg-3'>
                                        <img src={NCDF3} alt="" className='img-fluid' />
                                    </div>
                                    <div className='col-lg-9'>
                                        <h3>{t("NCD:NCD9")}</h3>
                                        <p className='small-text'>{t("NCD:NCD10")}</p>
                                    </div>
                                </div>
                            </div>
                            <div className='col-lg-6 mb-4'>
                                <div className='p-3 bg-white h-100'>
                                    <div className='col-lg-3'>
                                        <img src={NCDF4} alt="" className='img-fluid' />
                                    </div>
                                    <div className='col-lg-9'>
                                        <h3>{t("NCD:NCD11")}</h3>
                                        <p className='small-text'>{t("NCD:NCD12")}</p>
                                    </div>
                                </div>
                            </div>

                        </div>

                        <div className='text-center'><a href="https://app.nivesh.com/home/index" target="_blank" className='btn btn-primary'>{t('CTAFooter:InvestNow1')}</a></div>

                    </div>
                </div>
                <div className='gardient-red'>
                    <div className='container container-wrapper'>
                        <h2 className='text-white'>{t("NCD:NCD13")}</h2>
                        <p className='text-white'>{t("NCD:NCD14")}
                        </p>
                    </div>
                </div>
                <div className="container container-wrapper">
                    <h2>{t("NCD:NCD14-A")}
                    </h2>
                    <Tabs>
                        <TabList>
                            <Tab>{t("NCD:NCD15")}</Tab>
                            <Tab> {t("NCD:NCD19")}</Tab>
                            <Tab>{t("NCD:NCD21")}</Tab>

                        </TabList>
                        <TabPanel>
                            <p>
                            {t("NCD:NCD16")}<br/><br/>
                            {t("NCD:NCD17")}<br/><br/>
                            {t("NCD:NCD18")}
                            </p>

                        </TabPanel>
                        <TabPanel>
                            <p>{t("NCD:NCD20")}</p>

                        </TabPanel>
                        <TabPanel>
                            <p>{t("NCD:NCD22")}
                            </p>

                        </TabPanel>

                    </Tabs>
                </div>

                <NApi />
                <div className="container testimonials">
                    <div className="row">
                        <div className="col-lg-12">
                            <h2>{t("NCD:NCD23")}</h2>

                            {productinfo && productinfo.faqs.map(faq =>
                                <FAQ heading={t(`${faq.heading}`)} description={t(`${faq.description}`)} />
                            )}

                        </div>
                    </div>
                </div>
            </>

        )
    }
}

export default withTranslation()(NCD)
